<template>
  <div>
    {{ msg }} / {{ name }} / {{ age }} / {{ aaa }}
    <!-- {{ myobj1 }} / {{ myobj1.name }} / -->
    / {{ myobj2 }}
    <br />
    <InputText v-model="msg" />
    <br />
    <Button @click="chg1" label="변경1" />
  </div>
</template>

<script>
import { reactive, ref, toRefs, toRef } from '@vue/reactivity'
export default {
  data() {
    return {
      sample: ''
    }
  },
  setup() {
    const msg = ref('test')
    const name = ref('나의 이름')
    const myobj1 = reactive({ name: '네버다이', age: 444 })
    const myobj2 = reactive({ name: '네버다이2222', age: 2222 })

    const aaa = toRef(myobj2, 'name')

    const chg1 = () => {
      msg.value = '네버다이'
      name.value = '내이름....'
      myobj1.name = '너의 이름...'
      myobj1.age = 111
      aaa.value = 'ddddddddd'
    }

    return {
      msg,
      name,
      ...toRefs(myobj1),
      myobj2,
      aaa,
      chg1
    }
  },
  created() {},
  mounted() {},
  unmounted() {},
  methods: {}
}
</script>

<style></style>
